import axios from "axios";
import {LoginFormType} from "components/Login/Login";
import {saveProfileData} from "redux/profile_page_reducer";
import {FormInputsType} from "components/Profile/ProfileInfo/ProfileStatusWithHooks";

const instance = axios.create({
    withCredentials: true,
    headers: {"API-KEY" : "3f2a2590-abfa-42fb-bdbe-272bf5fe85c8"},
    baseURL: 'https://social-network.samuraijs.com/api/1.0/'
})

export const usersAPI = {
    getUsers (currentPage = 1, pageSize = 10) {
        // @ts-ignore
        return instance.get(`users?page=${currentPage}&count=${pageSize}`)
            .then(response => response.data)
    },

    unFollow (userId: number) {
        return instance.delete(`follow/${userId}` )
            .then(response => response.data)
    },

    follow (userId: number) {
        return instance.post(`follow/${userId}` )
            .then(response => response.data)
    }

}

export const ProfileAPI = {
    login (userId: string) {
        return instance.get(`profile/${userId}`)
                        .then(response => response.data)
    },
    getStatus (userId: string) {
        return instance.get(`profile/status/${userId}`)
    },
    updateStatus(status: string) {
        return instance.put(`/profile/status/`, {status})
    },
    savedPhoto(ava: string | Blob) {
        const formData = new FormData()
        formData.append('image', ava)
        return instance.put('/profile/photo', formData, {
            headers: {
                'Content-Type': 'multipart/form-data'
            }
        })
    },
    saveProfileData(formData: FormInputsType) {
        return instance.put('/profile', formData)
    }

}

export const authAPI = {
    authorization () {
        return instance.get(`auth/me`).then(response => response.data)
    },
    login (loginData: LoginFormType) {
        return instance.post(`auth/login`, loginData).then(response => response.data)
    },
    logout () {
        return instance.delete(`auth/login`).then(response => response.data)
    },
    getCaptchaImg () {
        return instance.get(`security/get-captcha-url`).then(response => response.data)
    },
}

